#outer{
    height:100vh;
    display:flex;
    align-items:center;
    justify-content: center;
}

#main__container{
    width: 300px;
    height:300px;
    border-radius: 50%;
    border: 10px solid black;
    background-color:red;
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
}
#main__text{
    color:whitesmoke;
    text-align:center;
    margin-bottom: 10px;
}

.button__collection>button{
    padding:15px;
    margin-right: 5px;
    border-radius:20px;
    border:none !important;
    outline: none
}
.button__collection>button:hover{
    opacity: .7;
    cursor: pointer;
}